import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';
import Page from 'components/LayoutComponents/Page';
import BtnSave from 'components/common/button/BtnSave';
import BtnCancel from 'components/common/button/BtnCancel';
import { Row, Card, Form, Spin, Input, message } from 'antd';
import { getFaculty, updateFaculty, createFaculty } from 'services/Facultys';

const handleInitialValue = initialValue => {
  const initialValueDefault = {
    id: '',
    code: '',
    labelInEnglish: '',
  };
  return { ...initialValueDefault, ...initialValue };
};

@Form.create({ name: 'formDepartmentTraining' })
class FormSystemTrainingPage extends Component {
  state = {
    systemId: false,
    initialValue: {},
    loading: false,
    disableCode: false,
  };

  componentDidMount = () => {
    const { match } = this.props;
    if (match.params.id) {
      this.setState({
        disableCode: true,
      });
    }

    if (match.params.id) {
      this.setState({ systemId: match.params.id, loading: true });
      getFaculty(match.params.id)
        .then(res => {
          const initialValue = handleInitialValue(res.data);
          this.setState({ initialValue, loading: false });
        })
        .catch(error => {
          console.log('error', error);
          this.setState({ loading: false });
        });
    }
  };

  handleSubmit = e => {
    const { match } = this.props;
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      if (!err) {
        if (match.params.id) {
          updateFaculty(match.params.id, values)
            .then(() => {
              message.success('Chỉnh sửa hệ đào tạo thành công');
              this.props.history.push('/department-training/list');
            })
            .catch(error => {
              console.log('res', error);
              // message.error(error.response.data.message);
            });
        } else {
          createFaculty(values)
            .then(() => {
              message.success('Thêm hệ đào tạo thành công');
              this.props.history.push('/department-training/list');
            })
            .catch(error => {
              console.log('res', error.response);
              // message.error(error.response.data.message);
            });
        }
      }
    });
  };

  render() {
    const { initialValue } = this.state;
    const { getFieldDecorator } = this.props.form;
    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 5 },
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 12 },
      },
      colon: false,
    };
    const { systemId } = this.state;

    return (
      <Card title={systemId ? 'Cập nhật khoa đào tạo' : 'Thêm khoa đào tạo'}>
        <Spin spinning={this.state.loading}>
          <Row>
            <Form {...formItemLayout} onSubmit={this.handleSubmit} className="login-form">
              <Form.Item hasFeedback label="Mã khoa" {...formItemLayout}>
                {getFieldDecorator('id', {
                  initialValue: initialValue.id,
                  rules: [
                    {
                      required: true,
                      message: 'Vui lòng nhập thông tin!',
                    },
                  ],
                })(<Input disabled={this.state.disableCode} />)}
              </Form.Item>
              <Form.Item {...formItemLayout} label="Tên khoa" hasFeedback>
                {getFieldDecorator('code', {
                  initialValue: initialValue.label,
                  rules: [
                    {
                      required: true,
                      message: 'Vui lòng nhập thông tin!',
                    },
                  ],
                })(<Input />)}
              </Form.Item>
              <Form.Item {...formItemLayout} label="Tên khoa Tiếng anh" hasFeedback>
                {getFieldDecorator('labelInEnglish', {
                  initialValue: initialValue.labelInEnglish,
                })(<Input />)}
              </Form.Item>
              <Form.Item {...formItemLayout} label=" ">
                <BtnSave htmlType="submit" />
                <BtnCancel href="#department-training/list" />
              </Form.Item>
            </Form>
          </Row>
        </Spin>
      </Card>
    );
  }
}

export default withRouter(props => (
  <Page {...props}>
    <FormSystemTrainingPage {...props} />
  </Page>
));



