import { request } from 'utils';

const prefix = '/api/categories';

export const getListFaculty = (page = 1) =>
  request({
    prefix,
    url: '/Faculty',
    params: {
      page,
      IsPagingEnabled: true,
    },
    method: 'GET',
  });
export const getFaculty = id =>
  request({
    prefix,
    url: `/Faculty/GetById`,
    method: 'GET',
    params: { id },
  });
export const deleteFaculty = id =>
  request({
    prefix,
    url: `/Faculty`,
    method: 'DELETE',
    data: { id },
  });
export const updateFaculty = (id, values) =>
  request({
    prefix,
    url: `/Faculty`,
    data: {
      id,
      label: values.code,
      labelInEnglish: values.labelInEnglish || '',
      regularId: values.regularId || '',
    },
    method: 'PUT',
  });

export const createFaculty = values =>
  request({
    prefix,
    url: '/Faculty',
    data: {
      id: values.id,
      label: values.code,
      labelInEnglish: values.labelInEnglish || '',
      regularId: values.regularId || '',
    },
    method: 'POST',
  });



